.animal-list-container {
    padding: 20px;
}

.filter-container {
    margin-bottom: 20px;
}

.animal-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
}

.animal-card {
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px;
    text-align: center;
    background-color: #fff;
    transition: box-shadow 0.3s;
}

.animal-card:hover {
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.animal-card img {
    max-width: 100%;
    border-radius: 8px;
}

.view-button {
    display: inline-block;
    margin-top: 10px;
    padding: 8px 12px;
    background-color: #007bff;
    color: white;
    border-radius: 4px;
    text-decoration: none;
}

.view-button:hover {
    background-color: #0056b3;
}

.image {
    width: 250px;
    height: 300px;
  }