/* Estilo para telas grandes */
@media (min-width: 1081px) {
  .card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 15%; /* 6 cards por linha, com espaço para margens */
    height: auto; /* Ajusta automaticamente a altura */
    padding: 20px;
    margin: 10px; /* Espaçamento entre os cards */
  }

  .pet-list {
    display: flex;
    flex-direction: row; /* Alinha os cards em linha */
    flex-wrap: wrap; /* Permite que os cards se movam para a próxima linha se não houver espaço */
    background-color: #B3E099;
    width: 90%;
    margin: auto;
    justify-content: space-between; /* Espaço uniforme entre os cards */
    padding: 40px;
    border-radius: 25px;
    align-items: flex-start; /* Alinha os itens no início */
    margin-bottom: 5%;
  }
}

/* Estilo para telas menores */
@media (max-width: 1080px) {
  .card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 80%; /* Largura das cartas em telas menores */
    height: auto; /* Ajusta automaticamente a altura */
    padding: 10px;
    margin: 10px auto; /* Margem vertical e centraliza as cartas */
  }

  .pet-list {
    display: flex;
    flex-direction: column; /* Alinha os cards em coluna em telas menores */
    background-color: #B3E099;
    width: 100%;
    justify-content: center; /* Centraliza a lista */
    padding: 40px;
    border-radius: 25px;
  }
}

.title {
  padding: 20px;
  text-align: center; /* Centraliza o título */
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.container {
  padding: 2px 16px;
}

.image {
  width: 100%; /* Torna a imagem responsiva */
  height: auto; /* Ajusta a altura de acordo com a largura */
  max-height: 300px; /* Limita a altura máxima da imagem */
}
