.header {
  font-style: italic;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.774) 0%, rgba(255, 255, 255, 0.733) 100%), url("../images/bg.png");
  min-height: 70vh;
  display: flex; /* Use flexbox para a disposição */
  align-items: center;
  justify-content: center;
  color: #1C2130;
  flex-direction: column; /* Alinha as logos em coluna por padrão */
}

.header-content {
  display: flex; /* Adicionei esta linha para alinhar as logos lado a lado */
  justify-content: space-between; /* Adicione espaço entre as logos */
  width: 100%; /* Certifique-se de que o contêiner ocupe a largura total */
  max-width: 1200px; /* Limitar a largura máxima do contêiner */
  padding: 0 20px; /* Adicione algum espaçamento lateral */
}

.options {
  flex: 1; /* Permite que cada coluna ocupe o mesmo espaço */
  text-align: center; /* Centraliza o conteúdo das opções */
  margin: 10px; /* Adiciona um pouco de margem */
}

.logoBanner {
  width: 100%; /* Ajuste a largura da logo */
  max-width: 300px; /* Limite a largura máxima da logo */
}

.botoes {
  display: flex;
  justify-content: center;
  margin: 10px;
  color: #ffffff;
  font-size: 2vb;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  text-align: center !important;
}

.botoes-primary {
  margin: 10px;
  color: #ffffff;
  font-size: 2vb;
  background-color: #028F76;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  text-align: center !important;
}

.botoes-sencondary {
  margin: 10px;
  color: #028F76;
  font-size: 2vb;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  text-align: center !important;
  border: 1px solid #028F76;
}

/* Media Query para telas menores */
@media (max-width: 768px) {
  .header-content {
    flex-direction: column; /* Empilha as logos em telas menores */
    align-items: center; /* Centraliza as logos */
  }

  .options {
    width: 100%; /* As opções ocupam 100% em telas menores */
    text-align: center; /* Centraliza o texto */
  }

  .logoBanner {
    max-width: 80%; /* Aumenta a largura máxima da logo para melhor ajuste */
  }
}
